.react-mapbox-ac-menu {
  width: auto;
  position: absolute;
  z-index: 9999;
  background-color: #fff;
  border: 1px solid #ccc;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-top: -1.3rem;
}

.react-mapbox-ac-input {
  margin-bottom: 1.5rem;
}

.react-mapbox-ac-suggestion {
  font-size: 18px;
  margin-bottom: .5rem;
  cursor: pointer;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.react-mapbox-ac-suggestion:hover {
  background-color: #58a;
}
.search-input{
   font: inherit;
   color: currentColor;
   width: 100%;
   border: 0;
   margin: 0;
   padding: 6px 0 7px;
   display: block;
   min-width: 0;
   box-sizing: content-box;
   background: none;
   -webkit-tap-highlight-color: transparent;
}

  
